.about {
    display: flex;
    color: #FF6060;
    flex-direction: column;
    padding-inline: 90px;
    margin-top: 15px;
}

.flex {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
}


@media (max-width: 800px) {
    .about {
        padding-inline: 0;
    }
}